* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
 
}
.country-list {
  list-style: none;
  padding-left: 0;
}
.country-list-item {
  display: flex;
  
}

.country-list-item:not(:last-child) {
  margin-bottom: 15px;
}

.country__name {
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 15px;
  font-size: 30px;
  font-weight: 700;
}

.country-bold {
  font-weight: 700;
}
